* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}
.list {
  list-style: none;
  padding: 0;
  margin: 0 auto;
}
.item {
  display: flex;
  align-items: center;
  font-weight: bold;
}
.title {
  margin: 0 auto;
}

.card-img {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}
.card-title {
  margin-left: 15px;
}
.card-pre-text {
  margin-right: 30px;
}
.list-info-title {
  margin-left: 20px;
}